.r-container {
    overflow: hidden;
    position: relative;
}
.r-head {
    margin-bottom: 2rem;
}
.r-card {
    gap: 0.6rem;
    padding: 1rem;
    background-repeat: 10px;
    min-width: max-content;
    margin: auto;
    transition: all 300ms ease-in;
  }
  
  .r-card > img {
    width: 100px;
    min-width: 17rem;
    border-radius: 10px;
  }
  
  .r-card > :nth-child(2) {
    font-size: 1.2rem;
    font-weight: 600;
  }
  
  .r-card > :nth-child(3) {
    font-size: 1.5rem;
  }
  
  .r-card > :nth-child(4) {
    font-size: 0.7rem;
    width: 15rem;
  }
  
  .r-card:hover {
    transform: scale(1.025); 
    cursor: pointer;
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0) 0%,
      rgba(136, 160, 255, 0.46) 217.91%
    );
    box-shadow: 0px 72px 49px -51px rgba(136, 160, 255, 0.21);
  }
  
  .Swiper-horizontal {
    overflow: visible;
}

.r-buttons {
    position: absolute;
    gap: 1rem;
    top: -4rem;
    right: 0;
}

.r-buttons button {
    font-size: 1.2rem;
    padding: 0.2rem 0.8rem;
    color: blue;
    border: none;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
}

.r-buttons > :nth-child(1) {
    background-color: #EEEEFF;
}

.r-buttons > :nth-child(2) {
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.25);
}
@media (max-width: 640px) {
  .r-head {
    align-items: center;
  }
  .r-buttons {
    position: initial;
  }
}